<template>
    <b-container fluid class="px-0 d-flex flex-column">
        <div class="main">
            <b-row no-gutters class="h-100">
                <b-col cols="3" class="h-100">
                    <div class="main-col border-right p-4">
                        <div class="logo-wrap text-center mb-3">
                            <img class="logo-wrap-img" src="@/assets/imgs/logo/logo.png" alt="PROOFY">
                        </div>
                        <div class="predicted-score-wrap border rounded-lg d-flex align-items-center p-3">
                            <div
                                class="predicted-score-wrap-text rounded-circle text-center text-primary font-weight-bolder h3 mr-3 my-0">
                                {{ totalScore }}
                            </div>
                            <div class="predicted-score-wrap-title d-flex flex-column justify-content-center flex-1">
                                <div class="predicted-score-wrap-title-text text-dark font-weight-bolder h5">Predicted Score
                                </div>
                                <div class="predicted-score-wrap-title-tip">
                                    <b-link
                                        class="p-0 text-secondary h6 m-0 text-underline text-truncate d-inline-block w-100"
                                        @click="handleModalShow('works')">
                                        See how predictive score works
                                    </b-link>
                                    <Modal :show="show.works" title="See how predictive score works"
                                        @onClose="handleModalClose('works')">
                                        <template v-slot:content>
                                            <p>Discover our AI-powered TOEFL Essay Scoring System!</p>
                                            <p class="my-4">Our innovative technology analyzes your essays on four crucial
                                                axes,
                                                each scored from 0 to 5: Addressing the Topic and Task, Organization and
                                                Development, Unity, Progression, and Coherence, and Language Use. Embrace
                                                accurate feedback with our system's ability to recognize subtle
                                                improvements,
                                                allowing for fractional scores like 4.5 or 5.</p>
                                            <p class="my-4">Enhance your TOEFL essay writing skills with our advanced
                                                scoring
                                                system and gain the competitive edge you need for exam success!</p>
                                        </template>
                                    </Modal>
                                </div>
                            </div>
                        </div>
                        <div class="border-top my-3"></div>
                        <div class="h6 text-dark mb-3">Organization Feedback</div>
                        <div class="score-wrap border rounded-lg p-3">
                            <div v-for="(item, index) in scoreResults " :key="index"
                                class="score-wrap-item d-flex justify-content-between align-items-center mb-3">
                                <div class="score-wrap-item-title w-75">
                                    <h5 class="mb-1">{{ item.name }}</h5>
                                </div>
                                <div class="score-wrap-item-score w-25 d-flex justify-content-end">
                                    <div class="score-wrap-item-score-box text-center font-weight-bold"
                                        :style="scoreStyles[index]">{{ item.score }}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div v-if="$auth.isAuthenticated" class="account-wrap mt-3">
                            <div class="account-btn-wrap">
                                <b-button block pill variant="primary" @click="handleModalShow('account')"><i
                                        class="bi bi-person-circle mr-2"></i>My Account</b-button>
                                <img class="account-star" src="@/assets/imgs/account-star.png" alt="">
                            </div>
                            <p v-if="memberType === 'pro'" class="mt-2 text-secondary text-center h7">Pro Member</p>
                            <p v-else-if="memberType === 'free'" class="mt-2 text-secondary text-center h7">As a free member
                                you can submit your essay
                                <b class="text-primary">2</b> times
                            </p>
                        </div>
                        <div class="writing-type-wrap w-100 p-4">
                            <b-dropdown id="dropdown-dropup" dropup text="TOEFL Writing" variant="outline-gray"
                                class="col-12 mt-3 p-0">
                                <b-dropdown-item href="#" class="w-100">TOEFL Writing</b-dropdown-item>
                            </b-dropdown>
                        </div>
                    </div>
                </b-col>
                <b-col cols="6" class="h-100">
                    <div class="main-col writing-wrap d-flex flex-column h-100">
                        <div class="writing-wrap-title">
                            <b-form-textarea
                                class="writing-wrap-title-textarea border-0 my-3 px-4 font-weight-bolder text-dark"
                                v-model="essayTitle" no-resize
                                placeholder='Add your topic here. For example, Do you agree or disagree with the following statement: “It is more important for students to study ...”'></b-form-textarea>
                        </div>
                        <div class="writing-wrap-content flex-1 h-100">
                            <b-form-textarea class="writing-wrap-content-textarea border-0 px-4 h-100 text-dark"
                                v-model="essayBody" no-resize placeholder="Add your essay response here."></b-form-textarea>
                        </div>
                        <div class="writing-wrap-options d-flex justify-content-between align-items-center p-4">
                            <div class="d-flex align-items-center text-secondary">
                                <small>Characters: {{ essayBody.length }}</small>
                                <span class="mx-2">|</span>
                                <small>Words: {{ wordsNum }}</small>
                            </div>
                            <div v-if="!$auth.loading">
                                <template v-if="!$auth.isAuthenticated">
                                    <b-button pill variant="primary" :disabled="!enableSubmit || loading.submit"
                                        @click="handleLogin"><b-spinner :hidden="!loading.submit"
                                            small></b-spinner>Submit</b-button>
                                </template>
                                <template v-else>
                                    <b-button v-if="generate" pill variant="outline-primary"
                                        @click="handleReset">Reset</b-button>
                                    <b-button v-else pill variant="primary" :disabled="!enableSubmit || loading.submit"
                                        @click="handleSubmit"><b-spinner :hidden="!loading.submit" class="mr-1"
                                            small></b-spinner>Submit</b-button>
                                </template>
                            </div>
                        </div>
                    </div>
                </b-col>
                <b-col cols="3" class="h-100">
                    <div class="main-col border-left d-flex flex-column p-4">
                        <div class="h6 text-dark mb-3">Organization Feedback</div>
                        <div v-for="(item, index) in scoreResults" class="mb-3">
                            <Collapse :key="index" v-if="item.hasDetail" :title="item.name" :content="item.detail">
                            </Collapse>
                        </div>
                    </div>
                </b-col>
            </b-row>
        </div>
        <Account v-if="$auth.isAuthenticated" :show="show.account" @onClose="handleModalClose('account')"
            @onCancelMembership="handleCancelMembership">
        </Account>
    </b-container>
</template>
  
<script>
import { isEmpty, isObject } from '@/utils/types'
import { essaySubmitAPI } from '@/api/writing'
import { checkUserPayStatusAPI } from '@/api/user'

import NProgress from 'nprogress' // Progress 进度条
import 'nprogress/nprogress.css' // Progress 进度条样式
import Modal from '@/components/modal/Modal'
import Account from '@/components/account'
import Collapse from '@/components/collapse'

export default {
    name: 'Writing',
    components: {
        Modal,
        Account,
        Collapse,
    },
    data() {
        return {
            scoreStyles: [
                { background: '#F1E2FF', color: '#864FB9', },
                { background: '#FFDDE2', color: '#C52C2C', },
                { background: '#E1E7FF', color: '#322F81', },
                { background: '#DDFDF5', color: '#339070', },
                { background: '#FFE6CA', color: '#7E2E13', },
            ],
            scoreResults: [
                { type: 'topic', name: 'Quality of Ideas', hasDetail: true, score: '0' },
                { type: 'grammar', name: 'Grammar', hasDetail: true, score: '0' },
                { type: 'organization', name: 'Organization', hasDetail: true, score: '0' },
                { type: 'structure', name: 'Writing Style', hasDetail: true, score: '0' },
                { type: 'overall_score', name: 'Summary', score: '0' },
            ],
            totalScore: 0, // 总分：overall_score 乘以 6 取整数部分
            essayTitle: '',
            essayBody: '',
            generate: false, // 已提交生成分数
            modalType: 'info',
            modalTitle: '',
            modalContent: '',
            loading: {
                submit: false
            },
            show: { // modal显示控制参数集合
                works: false,
                msg: false,
                account: false,
            },
            memberType: '',
            dismissSecs: 5,
            dismissCountDown: 0,
            showDismissibleAlert: false
        }
    },
    computed: {
        enableSubmit() {
            return !isEmpty(this.essayTitle.trim()) && !isEmpty(this.essayBody.trim())
        },
        wordsNum() {
            let trimBody = this.essayBody.replace(/\n|\r|^\s+|\s+$/gi, "").replace(/\s+/gi, " ")
            return isEmpty(trimBody) ? 0 : trimBody.split(' ').length
        },
        userId() {
            return this.$auth.user.sub && this.$auth.user.sub.replace('auth0|', 'auth0_') || ''
        }
    },
    watch: {
        '$auth.user.sub': {
            handler(newVal, oldVal) {
                if (!isEmpty(newVal)) {
                    this.checkUserPayStatus()
                }
            },
            immediate: true
        },
        '$auth.loading': {
            handler(newVal, oldVal) {
                if (!newVal) {
                    NProgress.done()
                }
            },
            immediate: true
        },
    },
    created() {
        NProgress.start()
    },
    mounted() { },
    methods: {
        // 检查用户付费状态
        checkUserPayStatus() {
            let params = {
                userId: this.userId,
            }

            checkUserPayStatusAPI(params).then(data => {
                this.memberType = data.is_paid ? 'pro' : 'free'
                this.$auth.user.isPro = data.is_paid

                NProgress.done()
            }).catch(error => {
                NProgress.done()
            })
        },
        // 登录
        handleLogin() {
            this.$auth.loginWithPopup();
        },
        handleModalShow(name) {
            this.show[name] = true
        },
        handleModalClose(name) {
            this.show[name] = false
        },
        // 提交
        handleSubmit() {
            this.loading.submit = true

            let params = {
                title: this.essayTitle,
                body: this.essayBody,
                userId: this.userId,
            }
            essaySubmitAPI(params).then(data => {
                if (!isObject(data)) {
                    this.$modal({
                        type: 'error',
                        title: 'Failed',
                        content: 'Please try again. Do you submit a valid essay topic and answer?',
                    })

                    this.loading.submit = false
                } else {
                    this.handleSubmitSuccess(data)
                    this.loading.submit = false
                }
            }).catch(error => {
                this.$modal({
                    type: 'error',
                    title: 'Failed',
                    content: error.response.data.message || 'Please try again. Do you submit a valid essay topic and answer?',
                })

                this.loading.submit = false
            })
        },
        // 提交成功
        handleSubmitSuccess(data) {
            let obj = {
                overall_score: {
                    score: data.overall_score
                }
            }
            data.score_breakdown.forEach(item => {
                obj[item.type] = {
                    score: item.score,
                    detail: item.detail
                }
            })

            this.scoreResults.forEach(item => {
                item.hasDetail && (item.detail = obj[item.type].detail)
                item.score = obj[item.type].score
            })

            this.totalScore = parseInt(data.overall_score * 6)
            this.generate = true
        },
        // 重置
        handleReset() {
            this.scoreResults = [
                { type: 'topic', name: 'Quality of Ideas', hasDetail: true, score: '0' },
                { type: 'grammar', name: 'Grammar', hasDetail: true, score: '0' },
                { type: 'organization', name: 'Organization', hasDetail: true, score: '0' },
                { type: 'structure', name: 'Writing Style', hasDetail: true, score: '0' },
                { type: 'overall_score', name: 'Summary', score: '0' },
            ]
            this.totalScore = 0
            this.essayTitle = ''
            this.essayBody = ''
            this.generate = false
        },
        handleCancelMembership() {
            // TODO 取消订阅成功提示
        }
    }
}
</script>
  
<style scoped lang="scss">
.main {
    height: 100vh;

    .main-col {
        position: relative;
        height: 100%;
        overflow: auto;
    }

    .logo-wrap {
        &-img {
            width: 5rem;
            max-width: 50%;
        }
    }

    .predicted-score-wrap {
        box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.05);

        &-text {
            width: 5rem;
            height: 5rem;
            line-height: 5rem;
            background: #D2F3FF;
        }

        &-title {
            max-width: calc(100% - 6rem);
        }
    }

    .score-wrap {
        box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.05);

        &-item {
            &-score {
                &-box {
                    width: 4rem;
                    height: 2.6rem;
                    line-height: 2.6rem;
                    border-radius: 1.3rem;
                }
            }
        }
    }

    .account-wrap {
        .account-btn-wrap {
            position: relative;
        }

        .account-star {
            position: absolute;
            top: -0.3rem;
            right: -0.3rem;
            width: 1.6rem;
        }
    }

    .writing-type-wrap {
        position: absolute;
        bottom: 0;
        left: 0;
    }

    .writing-wrap {

        &-title {
            &-textarea {
                font-size: 1.25rem;
            }
        }

        &-content {
            &-textarea {
                line-height: 1.8rem;
            }
        }
    }

    .suggestions-title-wrap {
        &-count {
            width: 2.2rem;
            height: 2rem;
            line-height: 2rem;
            border-radius: 1rem;
            background: #DDFDF5;
            color: #339070;
        }
    }

    .suggestions-content-wrap {
        &-empty-img {
            width: 20rem;
            max-width: 80%;
            margin-top: -7rem;
        }
    }
}

.form-control:focus {
    border-color: inherit;
    box-shadow: none;
}

.btn {
    padding: 0.8rem 2.3rem;
    font-weight: 500;
}
</style>



